  .styles_container__3TLur input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  /* Create a custom radio button */
  .styles_checkmark__316Ek {
    position: absolute;
    top: 0;
    left: 0;
    height: 17px;
    width: 17px;
    background-color: white;
    border: 1px solid #C1CFE0;
    border-radius: 50%;
  }

  .styles_container__3TLur input:checked ~ .styles_checkmark__316Ek {
    background: #4E86E4;
    border: 3px solid #C1CFE0;
    box-sizing: border-box;
  }

  /* Create the indicator (the dot/circle - hidden when not checked) */
  .styles_checkmark__316Ek:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the indicator (dot/circle) when checked */
  .styles_container__3TLur input:checked ~ .styles_checkmark__316Ek:after {
    display: block;
  }


